@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Libre Franklin", sans-serif;
}

body {
  width: 100%;
  height: 100vh;
  background: rgb(217, 255, 251);
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 0;
}

.TodoWrapper {
  width: 400px;
  height: 100%;
  background: rgb(91, 229, 236);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 20px 20px;

  border-radius: 0.5em;
  box-shadow: 2px 2px 10px rgb(13, 13, 13);
}

.TodoWrapper .title {
  width: 100%;
  height: 40px;
  color: rgb(13, 13, 13);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}
.title img {
  width: 40px;
  height: 40px;
}

.title h1 {
  font-size: 20px;
}

form {
  width: 100%;
  height: auto;
  font-size: 13px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 2px;

  margin-bottom: 20px;
}
form .todo-input {
  all: unset;
  width: 80%;
  height: 30px;
  background: transparent;
  border: 2px solid rgb(255, 255, 255);
  border-radius: 3px;
  padding: 0 10px;
}
form .todo-btn {
  all: unset;
  width: 20%;
  height: 30px;
  font-size: 10px;
  color: rgb(236, 236, 236);
  background: rgb(4, 56, 52);
  text-align: center;
  cursor: pointer;
  border-radius: 3px;
  padding: 0 5px;
}

.Todo {
  width: 100%;
  height: auto;
  color: rgb(30, 18, 18);
  background: rgb(236, 236, 236);
  font-size: 13px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  border-radius: 0.2em;
  cursor: pointer;
}

.completed {
  color: rgb(157, 155, 155);
  text-decoration: line-through;
}

.Todo .icons {
  width: 15%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.Todo .icon {
  width: 15px;
}
